import React, { useContext, useState } from 'react';
import './ActivitiesSection.css';
import image from '../../Assets/images/chatgpt_image.jpg';
import Svgs from '../../Assets/icons/Svgs';
import { DataContext } from '../../DataContext';
import { useNavigate } from 'react-router-dom';

const ActivitiesSection = ({ setIsActivities }) => {

  const [activities, setActivities] = useState([
    {_id: 0, user_id: 123, username: "Jihad", profilePic: "", reason: "He done bad things", title: "This post title", type: "delete post"},
    {_id: 0, user_id: 123, username: "Jihad", profilePic: "", reason: "He done bad things", title: "This post title", type: "block user"},
    {_id: 0, user_id: 123, username: "Jihad", profilePic: "", reason: "He done bad things", title: "This post title", type: "make admin"},
  ]);  
  const { 
    userID, set_navigateTo_userID, setIsMyProfile
  } = useContext(DataContext);
  const [result, setResult] = useState("");
  const navigate = useNavigate();

  const navigateToProfile = (id) => {
    if(id === userID){
        set_navigateTo_userID("");
        setIsMyProfile(true);
        navigate(`/profile/${0}`);
    } else {
        set_navigateTo_userID(id);
        setIsMyProfile(false);
        navigate(`/profile/${id}`);
    }
  };

  const getWhatHappened = (type) => {
    switch(type){
      case "delete post":
        return "deleted a post";
      case "delete comment":
        return "deleted a comment";
      case "block user":
        return "blocked a user";
      case "un-block user":
        return "un-block a user";
      case "remove admin":
        return "removed an admin";
      case "make admin":
        return "make a new admin";
      default:
        return "did something";
    };
  };

  return (

    <div className='ActivitiesSectionContainer'>
      
      <div className='returnDiv' onClick={() => setIsActivities(false)}>
          <Svgs type={"Exit"}/>
      </div>
      
      <div className='ActivitiesSection'>

        <ul>
          {activities.map((item) => (
            <li key={item._id} className='activity'>
              <div className='ActivityHeader'>
                <img src={image}/>
                <h2 onClick={() => navigateToProfile(item.user_id)}>{item.username} {getWhatHappened(item.type)}</h2>
                <label>{item.type}</label>
              </div>

              <div className='activityObject'>

              {/* "delete post", "delete comment", "block user", "un-block user", "remove admin", "make admin" */}

                  {item.type !== "delete post" && item.type !== "delete comment" && <div className='objectUser'>
                    <img src={image}/>
                    <h2>{"Jaheem"}</h2>
                  </div>}

                  <div className='reasonDiv'>
                    <h4>Reason: </h4>
                    <p>{item.reason}</p>
                  </div>

              </div>
            </li>
          ))}
        </ul>

      </div>

    </div>

  )
};

export default ActivitiesSection;
