import React, { useContext, useState } from 'react';
import './BlockedUserCard.css';
import { useNavigate } from 'react-router-dom';
import { DataContext } from '../../DataContext';
import image from '../../Assets/images/chatgpt_image.jpg';
import Svgs from '../../Assets/icons/Svgs';

const BlockedUserCard = ({ key, profilePic, userId, username, reason, blockedUserId }) => {
    
    const { 
        userID, set_navigateTo_userID, setIsMyProfile
    } = useContext(DataContext);
    const [isMenu, setIsMenu] = useState(false);
    const navigate = useNavigate();
    const [result, setResult] = useState("");

    const navigateToProfile = (id) => {
        if(id === userID){
            set_navigateTo_userID("");
            setIsMyProfile(true);
            navigate(`/profile/${0}`);
        } else {
            set_navigateTo_userID(id);
            setIsMyProfile(false);
            navigate(`/profile/${id}`);
        }
    };

    const unBlockUser = async() => {
        setResult("un blocking user");
    };


    return (

        <li key={key} className='blockedUser'>
            
            <div className='blockHeader'>
                <img src={image}/>
                <h2 onClick={() => navigateToProfile(userId)}>{username}</h2>
                <div className='menu'>
                    <Svgs type={"ThreeDotsMenu"} on_click={() => setIsMenu(!isMenu)}/>
                    <ul style={{scale: isMenu ? "1 1" : "1 0"}}>
                        <li onClick={unBlockUser}>Unblock</li>
                    </ul>
                </div>
            </div>
            
            <div className='blockedUser'>
                <div className='blockHeader'>
                    <img src={image}/>
                    <h2 onClick={() => navigateToProfile(userId)}>{username}</h2>
                </div>
                <label>Reason for block</label>
                <p>He was very rude</p>
            </div>

        </li>

    )
}

export default BlockedUserCard;
